.card {
  /* background: rgb(27, 31, 31); */
  
  border-radius: 2px;
  height: 175px;
  margin: 1rem;
  width: 150px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  opacity: 0.8;


  
  
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  margin-top: 1rem;
}
.card > .img-container > img {
  width: 55%;
}










.wrapper {

  width: 50%;
  display: flex;
  flex-flow: row wrap;
  padding: 10px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: none;
  margin: auto;
  
}

body {
  margin: 0;
  padding: 0;
}

.header {
  padding: 20px
}

nav {
  width: 100%;
  margin: 0;
  top: 0;
  background: #212121;
  color: #ccc;
  height: 100px;
}

ul {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  height: 100%;
}

li {
  height: 100%;
  flex: 1 1;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  -webkit-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
  cursor: pointer;
  font-size: 30px;
}

/* li:hover {
  background: #848484;
  color: #212121;
} */

.fixed-nav {
  position: fixed;
}

.content {
  width: 80%;
  margin: 0px auto;
  text-align: justify;
  padding-top: 20px;
}

.content > div {
  margin: 30px 0;
}

* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
  background-image: url(/clickygame/static/media/background.4e47f624.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position-x: -25rem;


}

